.list{
   display: flex;
   flex-direction: column;
   padding: 0;
   margin: auto;
}
.item{
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding:  10px 10px ;
   margin: 5px 0;
   border: 1px solid black;
}
.form {
   display: inline-flex;
flex-direction: column;
justify-content: space-around;
}
.label {
   display: flex;
   flex-direction: column;
   width: auto;
 }
 .input {
   height: 20px;
   width: 200px;
   margin: 0 auto 10px auto;
 }
 .itemTxt{
   width: 200px;
   margin: auto;
 }
 .button {
      cursor: pointer;
     
      margin: 5px auto 15px auto;
      width: auto;
      align-items: center;
 }
 .btnCont {
   cursor: pointer;
  
}